ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333333;
}

li {
    float: left;
}

li button {
    display: block;
    text-align: center;
    padding: 16px;
    text-decoration: none;
}

li button:hover {
    background-color: #111111;
}